<template>
  <div class="imageContainer">
    <a-form layout="inline">
      <a-form-item>
        <a-input placeholder="" v-model="searchKey" @change="searchKeyOnchange"
                 v-focus
                 style="width: 400px">
          <a-icon slot="prefix" type="search" style="color:rgba(0,0,0,.25)"/>
        </a-input>
      </a-form-item>

      <a-form-item>
        <a-space>
          <a-button html-type="reset" @click="reloadImageList">
            <a-icon type="reload"></a-icon>
            
          </a-button>

          <a-button type="primary" @click="pullImage">
            <a-icon type="download"></a-icon>
            
          </a-button>
        </a-space>
      </a-form-item>
    </a-form>
    <a-table :columns="columns" :data-source="imageList"
             style="margin-top: 30px">
    <span slot="action" slot-scope="text, record">
      <a-space>
        <a href="#" @click="detail(record.imageLongId)"></a>
        <a-divider type="vertical"></a-divider>
        <a href="#" @click="remove(record.imageLongId)"></a>
        <a-divider type="vertical"></a-divider>
        <a-dropdown>
          <a class="ant-dropdown-link" @click="e => e.preventDefault()"> <a-icon
              type="down"/> </a>
          <a-menu slot="overlay">
            <a-menu-item>
                <a href="#" @click="openNewContainerConfigModal(record.rep)"></a>
            </a-menu-item>
            <a-menu-item>
              <a href="#" @click="exportImg(record.imageLongId)"></a>
            </a-menu-item>
            <a-menu-item>
                <a href="#" @click="openReTagModal(record.rep)"></a>
            </a-menu-item>
            </a-menu>
        </a-dropdown>
      </a-space>
    </span>
    </a-table>


    <a-modal v-model="tagImageVisible" title="" okText="" cancelText="" @ok="callReTagApi">
      <a-form :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }">
        <a-form-item label=" Tag">
          <a-input v-model="oldTag" disabled/>
        </a-form-item>
        <a-form-item label=" Tag">
          <a-input @change="e=> this.newTag = e.target.value"/>
        </a-form-item>
      </a-form>
    </a-modal>

    <a-modal v-model="runImageVisible" title="" okText="" cancelText=""
             @ok="callRunNewContainerApi">
      <a-form-model :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }"
                    v-model="containerConfig">
        <a-form-model-item label="">
          <a-input v-model="containerConfig.imageName" disabled=""/>
        </a-form-model-item>

        <a-form-model-item label="">
          <a-input v-model="containerConfig.containerName" placeholder=""/>
        </a-form-model-item>

        <a-form-model-item label="">
          <a-input v-model="containerConfig.bindPort" placeholder=":(;)"/>
        </a-form-model-item>

        <a-form-model-item label="">
          <a-input v-model="containerConfig.env" placeholder="=(;)"/>
        </a-form-model-item>

        <a-form-model-item label="">
          <a-input v-model="containerConfig.volume" placeholder=":(;)"/>
        </a-form-model-item>

      </a-form-model>
    </a-modal>


    <a-modal v-model="pullImageVisible" title="" okText="" cancelText="" width="800px"
             @ok="callPullImageApi">
      <a-form-model :label-col="{ span: 2 }" :wrapper-col="{ span: 22 }"
                    v-model="containerConfig">
        <a-form-model-item label="">
          <a-input v-model="pullImageConfig.imageName"/>
        </a-form-model-item>
      </a-form-model>

      <pre style="overflow-y: auto; overflow-x:auto;height: 300px;background-color: #EFEFEF">{{pullLog}}</pre>

    </a-modal>


    <a-drawer
        placement="right"
        width="35%"
        @close="close"
        :visible="showDetail">
      <a-collapse :activeKey="['info','config']">
        <a-collapse-panel key="info" header="">
          <p style="text-wrap: inherit">ID: {{ this.imageInfo.Id }}</p>
          <p style="text-wrap: inherit"> ID: {{ this.imageInfo.Parent }}</p>
          <p>: {{ (this.imageInfo.Size / 1024 / 1024) .toFixed(2)}} M</p>
          <p>: {{ this.imageInfo.Architecture }}</p>
          <p>: {{ this.imageInfo.Os }}</p>
          <p>: {{ this.imageInfo.GraphDriver.Name }}</p>
          <p>Docker : {{ this.imageInfo.DockerVersion }}</p>
        </a-collapse-panel>
        <a-collapse-panel key="config" header="">
          <p>: {{ this.imageInfo.Config.Host }}</p>
          <p>: {{ this.imageInfo.Config.Domainname }}</p>
          <p>: {{ this.imageInfo.Config.ExposedPorts}}</p>
          <p>{{ this.imageInfo.Config.Env}}</p>
          <p>: {{ this.imageInfo.Config.Cmd}} </p>
          <p>: {{ this.imageInfo.Config.WorkingDir}} </p>
        </a-collapse-panel>
      </a-collapse>
    </a-drawer>
  </div>
</template>
<script>


  import {mapActions} from "vuex";
  import imageApi from "../api/ImageApi";
  import {guid} from '../utils/index'

  const columns = [
    {
      title: ' Id',
      key: 'imageId',
      dataIndex: 'imageId',
    },
    {
      title: ' Tag',
      dataIndex: 'rep',
      key: 'rep',
    },

    {
      title: '',
      dataIndex: 'size',
      key: 'size',
    },
    {
      title: '',
      key: 'created',
      dataIndex: 'created'

    },
    {
      title: '',
      key: 'action',
      scopedSlots: {customRender: 'action'},
    },
  ];

  export default {
    data() {
      return {
        searchKey: '',
        oldTag: '',
        newTag: '',
        showDetail: false,
        currentImageId: '',
        currentRep: '',
        columns,
        containerConfig: {
          imageName: '',
          containerName: '',
          bindPort: '',
          env: '',
          volume: ''
        },
        pullImageConfig: {imageName: ''},
        pullLog: '',
        pulling: false,
        pullImageVisible: false,
        runImageVisible: false,
        tagImageVisible: false,
      };
    }, mounted() {
      this.updateImage()
    },
    computed: {
      imageList: function () {
        let allImageList = this.$store.state.image.imageList;
        if (this.searchKey === '' || this.searchKey.trim() === '') {
          return allImageList
        }
        return allImageList
        .filter(i => i.rep.indexOf(this.searchKey) >= 0 || i.imageId.indexOf(this.searchKey) >= 0)
      }, imageInfo: function () {
        return this.$store.state.image.imageInfo;
      }, containerList: function () {
        return this.$store.state.container.containerList;
      }
    },
    methods: {
      ...mapActions({
        updateImage: "updateImageList",
        getImageInfo: "getImageInfo",
        removeImage: "removeImage"
      }),
      searchKeyOnchange: function (e) {
        this.searchKey = e.target.value
      },
      detail: function (imageId) {
        this.showDetail = true;
        this.currentImageId = imageId;
        this.getImageInfo({imageId})
      },
      remove: function (imageId) {
        let context = this;
        this.$confirm({
          title: `?`,
          okText: '',
          cancelText: '',
          onOk: () => {
            context.$axios.get(`/api/image/${imageId}/remove/false`).then(res => {
              let data = res.data;
              if (data.Code === 'OK') {
                context.$message.info('!');
                this.updateImage()
              } else {
                context.$confirm({
                  title: `?`,
                  content: data.Msg,
                  okText: '',
                  cancelText: '',
                  onOk: () => this.forceRemove(imageId)
                })
              }
            }).catch(e => {
              this.$notification['error']({
                message: '',
                description: " Docker , Docker ",

              });
            })

          },
          class: 'test',
        });

      },
      forceRemove: function (imageId) {
        this.$axios.get(`/api/image/${imageId}/remove/true`).then(res => {
              let {Code, Msg} = res.data;
              if (Code === 'OK') {
                this.$message.info('!');
                this.updateImage()
              } else {
                this.$message.error(Msg);
              }
            }
        ).catch(e => {
          this.$message.error(" Docker , Docker ");
        })
      },
      reloadImageList: function () {
        this.updateImage()
        this.$message.info('');
      },
      pullImage: function () {
        this.pullImageVisible = true;
      },
      close: function (e) {
        this.showDetail = false;
      },
      openNewContainerConfigModal: function (imageName) {
        this.containerConfig.imageName = imageName;
        this.runImageVisible = true;
      }, callPullImageApi() {
        if (this.pulling) {
          this.$message.error(",")
          return;
        }

        this.pulling = true
        this.pullLog = "<br/> ..."
        let imageName = this.pullImageConfig.imageName;
        if (imageName.trim() === '') {
          this.$message.error("")
          return
        }

        let key = guid()
        this.$message.loading({content: "....", key, duration: 0})
        this.$axios.get(`/api/image/pull?refStr=${imageName}`)
        .then(res => {
          let {Data, Code, Msg} = res.data
          if (Code === 'OK') {
            this.pullLog = Data
            this.$message.info({content: "", key})
          } else {
            this.pullLog = Msg
            this.$message.warning({content: Msg, key})
          }
        }).catch(e => {
          this.$message.error({content: '', key})
          this.pullLog = ""
        })
        this.pulling = false
      }, exportImg: function (imageId) {
        let key = guid()
        this.$message.loading({content: "....", key, duration: 0});

        let config = {
          withCredentials: true,
          timeout: 600000
        }
        this.$axios.create(config).get(`/api/image/${imageId}/save`, {responseType: 'blob'}).then(
            (res) => {
              this.download(res.data, `${imageId}.tar.gz`)
              this.$message.info({content: "....", key});
            })
        .catch(e => {
          this.$message.error({content: ", Docker ", key});
        })
      }, openReTagModal: function (oldTag) {
        this.tagImageVisible = true
        this.oldTag = oldTag
      }, async callRunNewContainerApi() {
        let res = await imageApi.runNewContainer(this.containerConfig)
        let {Code, Msg} = res.data
        if (Code === 'OK') {
          this.$message.info('!');
          this.runImageVisible = false;
        } else {
          this.$message.error(Msg);
        }
      },
      download: function (data, fileName) {
        if (window.navigator && window.navigator.msSaveOrOpenBlob) {
          let blob = new Blob([data], {
            type: 'application/vnd.ms-excel'
          })
          window.navigator.msSaveOrOpenBlob(blob, fileName)
        } else {
          let blob = new Blob([data])
          let downloadElement = document.createElement('a')
          let href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = fileName
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
        }
      }, callReTagApi: function () {
        if (this.newTag === '' || this.newTag.trim() === '') {
          this.tagImageVisible = false
          this.$notification['warning']({
            message: '',
            description: "tag "
          });
          return
        }

        if (this.newTag.trim() === this.oldTag.trim()) {
          this.tagImageVisible = false
          this.$notification['warning']({
            message: '',
            description: "tag"
          });
          return
        }

        let data = {
          source: this.oldTag,
          tag: this.newTag
        }
        this.$axios.get(
            `/api/image/tag`, {params: data}
        )
        .then((res) => {
          let data = res.data;
          if (data.Code === 'OK') {
            this.$message.info('!');
            this.tagImageVisible = false
            this.updateImage()
          } else {
            this.$notification['warning']({
              message: '',
              description: data.Msg
            });
          }
        })
        .catch(e => {
          this.$notification['warning']({
            message: '',
            description: ", Docker "
          });
        })
      }
    }
  }
  ;
</script>

<style scoped>
  .ant-drawer-body {
    padding: 0 !important;
  }
</style>
