import axios from "axios";

function runNewContainer(containerConfig) {
  let apiResp = axios.get(`/api/container/run`, {params: containerConfig})
  apiResp.catch(e => {
    this.$notification['warning']({
      message: ``,
      description: `, Docker `
    });
  });
  return apiResp

}

export default {
  runNewContainer: runNewContainer
}
